.header {
    font-family: "Barlow Condensed", sans-serif;

    .toggle-menu {
        display: none;
        outline: none;
        background: transparent;
        border: none;
        padding: 0;

        span {
            width: 30px;
            height: 2px;
            background: $dark;
            display: block;
            position: relative;

            &::before,
            &::after {
                content: "";
                width: 100%;
                height: 2px;
                background: $dark;
                position: absolute;
                left: 0;
            }

            &::before {
                top: 10px;
            }

            &::after {
                bottom: 10px;
            }
        }
    }

    .main-menu {

        &>li {
            position: relative;

            &>a {
                padding: 8px 20px;
                font-size: 18px;
                font-weight: 600;
                color: $dark;
                text-transform: uppercase;
                position: relative;

                &::before {
                    content: "";
                    width: 0;
                    height: 2px;
                    background: $brand;
                    position: absolute;
                    bottom: -10px;
                    left: 0;
                    transition: all .3s;
                    z-index: 1;
                }
            }

            &:hover {

                &>a {
                    color: $brand;

                    &::before {
                        left: 20px;
                        width: calc(100% - 40px);
                    }
                }
            }

            &.active {
                &>a {
                    color: $brand;

                    &::before {
                        left: 20px;
                        width: calc(100% - 40px);
                    }
                }
            }
        }
    }
}